<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">用户权限</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">角色管理</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
          <div class="button-content">
            <Button type="primary" @click="addfun(0)" v-authcontrol="{menuCode:'112003002001'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
            <!-- <Button type="primary">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>-->
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{pageSize*(pageNum-1)+index+1}}</td>
                    <td>
                      <span class="spanBox" @click="modify(item.roleCode)">修改</span>
                      <span class="spanBox" @click="deleteOne(item.roleCode)">删除</span>
                      <span class="spanBox" @click="roleSelectFun(item.roleCode)">权限</span>
                      <span class="spanBox">导出</span>
                    </td>
                    <td>{{item.roleName}}</td>
                    <td>{{item.roleCode}}</td>
                    <td>{{item.roleDesc}}</td>
                    <td>{{item.notes}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page
              :total="rowCount"
              :current.sync="pageNum"
              @on-change="pageChange"
              :page-size="pageSize"
              @on-page-size-change="pageSizefun"
              show-elevator
              show-sizer
            />
          </div>
        </div>
      </div>
    </div>
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal1" width="1000" class="dialog" title="角色设置">
      <div>
        <Form
          ref="formValidateModel1"
          :model="formValidateModel1"
          :rules="ruleValidateModel1"
          :label-width="80"
        >
          <FormItem label="角色名称" prop="roleName">
            <Input
              v-model="formValidateModel1.roleName"
              placeholder="请输入角色名称"
              :disabled="modalDisableFlag"
            ></Input>
          </FormItem>
          <FormItem label="角色代码" prop="roleCode">
            <Input
              v-model="formValidateModel1.roleCode"
              placeholder="请输入角色代码"
              :disabled="modalDisableFlag"
            ></Input>
          </FormItem>
          <FormItem label="角色描述" prop="roleDesc">
            <Input v-model="formValidateModel1.roleDesc" placeholder="请输入角色描述"></Input>
          </FormItem>
          <FormItem label="备注信息" prop="notes">
            <Input
              v-model="formValidateModel1.notes"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入备注信息"
            ></Input>
          </FormItem>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button
            type="primary"
            @click="mode1Save('formValidateModel1')"
            v-if="modal1"
          >保存</Button>
          <Button style="margin-left: 8px" @click="modal1=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal2" width="1000" class="dialog" title="权限">
      <div class="jurisdictionModal flexStart">
        <div class="jurisdictionModal-1">
          <Tree ref="tree1" :data="data4" show-checkbox multiple @on-check-change="getSelectLength"></Tree>
        </div>
        <div class="jurisdictionModal-2">
          <div class="jurisdictionModal-2-1">数据权限</div>
          <div class="jurisdictionModal-2-2">
            <!-- ------------ -->
            <Form :model="formItem" :label-width="80">
              <FormItem label>
                <RadioGroup v-model="formItem.levelId" @on-change="updateShowSelectTreeDates">
                  <Radio v-for="item in levelIds" :label="item.code" :key="item.code">{{item.name}}</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label>
                <treeselect
                  v-if="showSelectTreeDates"
                  v-model="formItem.unitIds"
                  :multiple="true"
                  :options="selectTreeDates"
                  :max-height="100"
                  style="position:absolute;top:15px;left:0px"
                />
              </FormItem>
            </Form>
            <!-- ------------ -->
          </div>
        </div>
      </div>
      <div slot="footer" class="flexCenter button-content" style="margin:0">
        <Button type="primary" @click="mode2Save">保存</Button>
        <Button style="margin-left: 8px" @click="modal2=false">取消</Button>
      </div>
    </Modal>
    <!-- ===========弹框 -->
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
  created() {
    let that = this;
    let data = {
      pageSize: this.pageSize,
      pageNum: this.pageNum
    };
    data = JSON.stringify(data);
    this.axios
      .post("/system/role/roleInfo", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.rowCount = response.data.total;
          that.tableBody = response.data.records;
        }
      })
      .catch(error => {});
  },
  computed: {
    radioVal() {
      return this.formItem.radio;
    }
  },
  watch: {
    radioVal(val1) {
      if (val1 == "female3") {
        this.roleSelectShow = true;
      } else {
        this.roleSelectShow = false;
      }
    }
  },
  data() {
    const validateRoleCode = (rule, value, callback) => {
      if (!this.roleCodeLegal) {
        return callback(new Error("角色代码已存在"));
      } else {
        callback();
      }
    };
    const validateRoleName = (rule, value, callback) => {
      if (!this.roleNameLegal) {
        return callback(new Error("角色名称已存在"));
      } else {
        callback();
      }
    };
    return {
      // ==============弹框二
      roleSelectShow: false, //判定弹框下拉框是否显示
      menuCodes: ["113002001", "1268"],
      formItem: {
        levelId: "",
        unitIds: null
      },
      selectLength: 0,
      roleCodeLegal: true,
      roleNameLegal: true,
      levelIds: [],
      showSelectTreeDates: false,
      selectTreeDates: [],
      pageNum: 1,
      rowCount: 0,
      pageSize: 10,
      modal2: false,
      data4: [
        // {
        //   title: "业务洗钱风险评估",
        //   expand: true,
        //   selected: true,
        //   children: [
        //     {
        //       title: "查询",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "新增",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "修改",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "历史",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "状态",
        //       expand: true,
        //       checked: true
        //     }
        //   ]
        // },
        // {
        //   title: "产品洗钱风险评估",
        //   expand: true,
        //   selected: true,
        //   children: [
        //     {
        //       title: "查询",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "新增",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "修改",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "历史",
        //       expand: true,
        //       checked: true
        //     },
        //     {
        //       title: "状态",
        //       expand: true,
        //       checked: true
        //     }
        //   ]
        // }
      ],
      // ==============弹框二
      // ===============弹框一
      modalDisableFlag: true, //修改与新增弹框内容禁用标识
      formValidateModel1: {
        roleName: "", //角色名称
        roleCode: "", //角色代码
        roleDesc: "", //角色描述
        notes: "" //说明
      },
      ruleValidateModel1: {
        roleName: [
          {
            required: true,
            message: "请输入角色名称",
            trigger: "blur"
          },
          {
            type: "string",
            max: 64,
            message: "长度最大不能超过64",
            trigger: "blur"
          },
          {
            validator: validateRoleName,
            trigger: "xxxx"
          }
        ],
        notes: [
          {
            type: "string",
            max: 100,
            message: "长度最大不能超过100",
            trigger: "blur"
          }
        ],
        roleDesc: [
          {
            type: "string",
            max: 100,
            message: "长度最大不能超过100",
            trigger: "blur"
          }
        ],
        roleCode: [
          {
            required: true,
            message: "请输入角色代码",
            trigger: "blur"
          },
          {
            type: "string",
            max: 32,
            message: "长度最大不能超过32",
            trigger: "blur"
          },
          {
            validator: validateRoleCode,
            trigger: "xxxx"
          }
        ]
      },
      modal1: false,
      //   ============弹框一
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "角色名称" },
        { name: "角色代码" },
        { name: "角色描述" },
        { name: "备注" }
      ],
      tableBody: []
    };
  },
  methods: {
    getSelectLength(nodes) {
      this.selectLength = nodes.length;
    },
    mode1Save(name) {
      let that = this;
      if (that.modalDisableFlag != true) {
        let valid = true;
        this.roleCodeLegal = true;
        this.roleNameLegal = true;
        this.$refs[name].validate(vali => {
          valid = vali;
        });
        if (!valid) {
          return;
        }
        let dd = {
          roleName: this.formValidateModel1.roleName,
          roleCode: this.formValidateModel1.roleCode
        };
        this.axios
          .post("/system/role/checkRoleCodeAndName", JSON.stringify(dd), {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code == "200") {
              that.roleCodeLegal =
                response.data == "201" || response.data == "203" ? false : true;
              that.roleNameLegal =
                response.data == "202" || response.data == "203" ? false : true;
              this.$refs[name].validate(valid => {
                if (valid) {
                  let data = JSON.stringify(that.formValidateModel1);
                  this.axios
                    .post("/system/role/addRole", data, {
                      headers: { "Content-Type": "application/json" }
                    })
                    .then(response => {
                      if (response.code == "200") {
                        that.$Message.success("保存成功");
                        setTimeout(() => {
                          that.modal1 = false;
                          that.rowCount = that.rowCount + 1;
                          that.pageChange(
                            Math.ceil(that.rowCount / that.pageSize)
                          );
                        }, 2000);
                      }
                    })
                    .catch(error => {});
                }
              });
            }
          })
          .catch(error => {});
      } else {
        this.$refs[name].validate(valid => {
          if (valid) {
            let data = JSON.stringify(that.formValidateModel1);
            this.axios
              .post("/system/role/updataRoleInfo", data, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  that.$Message.success("保存成功");
                  setTimeout(() => {
                    that.modal1 = false;
                  }, 2000);
                }
              })
              .catch(error => {});
          }
        });
      }
    },
    mode2Save() {
      // -----------
      let that = this;
      if (
        this.$refs.tree1.getCheckedNodes().length == 0 ||
        this.formItem.levelId == ""
      ) {
        this.$Message.warning("操作权限和数据权限至少各选中一项");
        return;
      }
      let data = {
        menu: this.data4,
        roleCode: this.roleCode,
        levelId: this.formItem.levelId,
        unitIds: this.formItem.unitIds
      };
      data = JSON.stringify(data);
      this.axios
        .post("/system/role/setRoleAuthView", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.$Message.success("保存成功");
            setTimeout(() => {
              that.modal2 = false;
            }, 2000);
          }
        })
        .catch(error => {});
      // -------------
    },
    roleSelectFun(val) {
      this.roleCode = val;
      //调用弹框2
      let that = this;
      let params = new FormData();
      params.append("roleCode", val);
      this.axios
        .post("/system/role/getRoleAuthView", params, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            that.data4 = response.data.menu;
            that.formItem = response.data.roleChecked;
            that.levelIds = response.data.level;
            that.selectTreeDates = response.data.unitTree;
            that.selectTreeCleanUp(that.selectTreeDates);
            that.levelIds.forEach(element => {
              if (element.code == that.formItem.levelId)
                that.showSelectTreeDates = element.flag;
            });
          }
        })
        .catch(error => {});
      this.modal2 = true;
    },
    updateShowSelectTreeDates(code) {
      for (let i = 0; i < this.levelIds.length; i++) {
        if (this.levelIds[i].code == code) {
          this.showSelectTreeDates = this.levelIds[i].flag;
          return;
        }
      }
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    pageChange(pageNo) {
      this.pageNum = pageNo * 1;
      let that = this;
      let data = {
        pageSize: this.pageSize,
        pageNum: this.pageNum
      };
      data = JSON.stringify(data);
      this.axios
        .post("/system/role/roleInfo", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.rowCount = response.data.total;
            that.tableBody = response.data.records;
          }
        })
        .catch(error => {});
    },
    pageSizefun(a) {
      this.pageSize = a * 1;
      if (this.pageNum == 1) {
        this.pageChange(1);
      }
    },
    watchfun() {
      this.$router.push({ name: "dataExtractionApplyDetail" });
    },
    addfun(index) {
      this.modalDisableFlag = false;
      this.formValidateModel1 = {
        roleName: "", //角色名称
        roleCode: "", //角色代码
        roleDesc: "", //角色描述
        notes: "" //说明
      };
      this.$refs["formValidateModel1"].resetFields();
      this.modal1 = true;
    },
    deleteOne(code) {
      this.$Modal.confirm({
        title: "是否删除？",
        okText: "确认",
        cancelText: "取消",
        onOk: () => {
          let data = JSON.stringify({ roleCode: code });
          this.axios
            .post("/system/role/deleteRole", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("删除成功");
                this.rowCount--;
                this.pageNum = Math.ceil(this.rowCount / this.pageSize);
                let data = {
                  pageSize: this.pageSize,
                  pageNum: this.pageNum
                };
                data = JSON.stringify(data);
                this.axios
                  .post("/system/role/roleInfo", data, {
                    headers: { "Content-Type": "application/json" }
                  })
                  .then(response => {
                    if (response.code == "200") {
                      this.rowCount = response.data.total;
                      this.tableBody = response.data.records;
                    }
                  })
                  .catch(error => {});
              } else {
                this.$Message.error(response.message);
              }
            })
            .catch(error => {});
        },
        onCancel: () => {}
      });
    },
    modify(code) {
      let that = this;
      this.$refs["formValidateModel1"].resetFields();
      this.formValidateModel1 = {
        roleName: "", //角色名称
        roleCode: "", //角色代码
        roleDesc: "", //角色描述
        notes: "" //说明
      };
      let data = JSON.stringify({ roleCode: code });
      this.axios
        .post("/system/role/getRoleOne", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.formValidateModel1 = response.data;
            that.modalDisableFlag = true;
            that.modal1 = true;
          }
        })
        .catch(error => {});
    },
    rolefun() {
      //调用弹框2
      this.modal2 = true;
    }
  }
};
</script>
<style scoped>
.rightTopbtn {
  margin: 15px 4px;
  float: right;
}
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
.roleBox {
  padding: 3px 0;
  cursor: pointer;
  color: blue;
}
.roleBox:hover {
  background-color: RGB(241, 243, 244);
}
</style>



